@import url(https://fonts.googleapis.com/css2?family=Baloo+2:wght@400;500&family=Zen+Dots&display=swap);
html,body,div,span,applet,object,iframe,h1,h2,h3,h4,h5,h6,p,blockquote,pre,a,abbr,acronym,address,big,cite,code,del,dfn,em,img,ins,kbd,q,s,samp,small,strike,strong,sub,sup,tt,var,b,u,i,center,dl,dt,dd,ol,ul,li,fieldset,form,label,legend,table,caption,tbody,tfoot,thead,tr,th,td,article,aside,canvas,details,embed,figure,figcaption,footer,header,hgroup,menu,nav,output,ruby,section,summary,time,mark,audio,video{margin:0;padding:0;border:0;font-size:100%;font:inherit;vertical-align:baseline}article,aside,details,figcaption,figure,footer,header,hgroup,menu,nav,section{display:block}body{line-height:1}ol,ul{list-style:none}blockquote,q{quotes:none}blockquote:before,blockquote:after,q:before,q:after{content:"";content:none}table{border-collapse:collapse;border-spacing:0}.h1{font-family:"Zen Dots",cursive;color:#fff;font-size:3rem;text-transform:uppercase}@media(max-width: 768px){.h1{margin-left:1rem;font-size:2rem}}.h2{font-family:"Baloo 2",cursive;font-weight:500;font-size:3.5rem;color:#fff}@media(max-width: 768px){.h2{font-size:2rem}}.listItem{font-family:"Baloo 2",cursive;font-weight:400;font-size:1.9rem;line-height:1.6;color:#fff;cursor:pointer}.listItem:hover{text-decoration:underline}@media(max-width: 768px){.listItem{margin:0;font-size:1.5rem}}.active{color:orange}#root{height:100%;display:grid;grid-template-columns:10% 25% 55% 10%;grid-template-rows:15% auto;grid-template-areas:". header header ." ". aside main ." ". footer footer ."}@media(max-width: 768px){#root{grid-template-columns:100%;grid-template-rows:10% auto;grid-template-areas:"header" "aside" "main" "footer"}#root .header{grid-area:header}#root .aside{grid-area:aside;margin-left:2rem}#root .main{grid-area:main;margin-top:-10rem}#root .footer{grid-area:footer}}.header{grid-area:header}.aside{grid-area:aside}.main{grid-area:main;align-self:start;justify-self:start}.footer{grid-area:footer}body{height:100vh;margin:0;background:#000;background:linear-gradient(143deg, black 0%, #80b3ab 100%);background-repeat:no-repeat;background-size:cover}.container{text-align:center}.aside{display:flex;flex-direction:column}.header{margin-top:2rem}.footer{position:absolute;bottom:0;left:50%;margin:0;font-size:1.5rem;color:#fff;margin-bottom:.4rem}.figure{margin-top:6rem}@media(max-width: 768px){.figure{margin-top:1rem}}#figure{-webkit-filter:url(#pencil3);filter:url(#pencil3)}#reload{cursor:pointer}.react-icons{width:30px;height:30px;color:orange;vertical-align:middle;cursor:pointer}
